import React, {ChangeEvent, useCallback, useState} from 'react';
import {
    Accordion,
    AccordionDetails,
    AccordionSummary,
    Box,
    Button,
    Divider,
    FormGroup,
} from '@material-ui/core';
import {withStyles} from '@material-ui/core/styles';
import {ICookieConsent, setCookie} from './Cookie';
import {ReactSVG} from 'react-svg';
import CookieBannerFormControlLabel, {
    CookieChoiceType,
    ICookieBannerFormControlLabelProps,
} from './CookieBannerFormControlLabel';
import WgTypography from "./WgTypography";
import {TextType} from "../types/Types";


const WgAccordion = withStyles({
    root: {
        boxShadow: 'none',
    },
})(Accordion);

const WgAccordionSummary = withStyles(() => ({
    root: {
        minHeight: '0px',
        padding: 0,
        '&$expanded': {
            minHeight: '0px',
        },
    },
    content: {
        margin: '1rem 0px',
        '&$expanded': {
            margin: '0',
        },
    },
    expanded: {
        minHeight: 0,
    },
    expandIcon: {
        marginRight: '5px',
        padding: '0px',
    },
}))(AccordionSummary);

const WgAccordionDetails = withStyles(() => ({
    root: {
        padding: 0,
    },
}))(AccordionDetails);

interface ICookieBannerProps {
    onClose: () => void;
    text: TextType;
    arrow: string;
}

interface ICookeBannerChoicesInputElement extends HTMLInputElement {
    name: CookieChoiceType;
}

function CookieBanner({onClose, text, arrow}: ICookieBannerProps): JSX.Element {
    const [hasChanged, setHasChanged] = useState(false);
    const [choices, setChoices] = useState<
        Record<CookieChoiceType, ICookieBannerFormControlLabelProps>
    >(() => text.choices);
    const handleClickButton = useCallback(
        (consent: ICookieConsent) => () => {
            setCookie('CookieConsent', JSON.stringify(consent), 30);
            onClose();
        },
        [onClose],
    );

    const handleChange = useCallback(
        (event: ChangeEvent<ICookeBannerChoicesInputElement>) => {
            setHasChanged(true);
            // eslint-disable-next-line @typescript-eslint/naming-convention
            const {checked, name} = event.target;
            setChoices((prevState) => {
                const nextState = {...prevState};
                nextState[name].checked = checked;
                return nextState;
            });
        },
        [],
    );

    const handleSubmit = useCallback(() => {
        handleClickButton({
            necessary: choices.necessary.checked,
            preferences: choices.preferences.checked,
            statistics: choices.statistics.checked,
            marketing: choices.marketing.checked,
        })();
    }, [
        choices.marketing.checked,
        choices.necessary.checked,
        choices.preferences.checked,
        choices.statistics.checked,
        handleClickButton,
    ]);

    return (
        <Box m="20px 20px 0 20px">
            <WgTypography noWrap text="contentInfo" color="primary">
                {text.messages.title}
            </WgTypography>

            <Box marginTop="1rem" marginBottom="1.5rem">
                <WgTypography color="textPrimary" text="sentenceSmall">
                    {text.messages.subtitle}
                </WgTypography>
            </Box>

            <Divider light />
            <WgAccordion>
                <WgAccordionSummary
                    expandIcon={<ReactSVG src={arrow}/>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <WgTypography
                        color="textPrimary"
                        text="content"
                        header="headerSmall"
                    >
                        {text.messages.showDetails}
                    </WgTypography>
                </WgAccordionSummary>
                <WgAccordionDetails>
                    <FormGroup onChange={handleChange}>
                        {Object.values(choices).map(
                            ({name, checked, disabled, title, text}) => (
                                <CookieBannerFormControlLabel
                                    key={name}
                                    name={name}
                                    checked={checked}
                                    disabled={disabled}
                                    title={title}
                                    text={text}
                                />
                            ),
                        )}
                    </FormGroup>
                </WgAccordionDetails>
            </WgAccordion>
            <Divider light />
            <Box
                display="flex"
                justifyContent="center"
                marginTop="1.5rem"
                marginBottom="0.5rem"
                width="100%"
            >
                <Button
                    variant="contained"
                    color="primary"
                    fullWidth
                    aria-label="accept"
                    onClick={handleClickButton({
                        necessary: true,
                        preferences: true,
                        statistics: true,
                        marketing: true,
                    })}
                >
                    {text.messages.accept}
                </Button>
            </Box>
            <Box
                display="flex"
                justifyContent="center"
                marginTop="0.5rem"
                marginBottom="1rem"
                width="100%"
            >
                {hasChanged ? (
                    <Button
                        onClick={handleSubmit}
                        variant="outlined"
                        color="secondary"
                        fullWidth
                        aria-label="submit"
                    >
                        {text.messages.submit}
                    </Button>
                ) : (
                    <Button
                        variant="outlined"
                        fullWidth
                        aria-label="only essentials"
                        onClick={handleSubmit}
                    >
                        {text.messages.essentials}
                    </Button>
                )}
            </Box>
        </Box>
    );
}

export default React.memo(CookieBanner);
