import React from 'react';
import {createStyles, makeStyles, Theme, Typography} from '@material-ui/core';
import {TypographyProps} from '@material-ui/core/Typography/Typography';
import clsx from 'clsx';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        // General style
        fitContent: {
            maxWidth: 'fit-content',
        },

        // Text Style
        red: {
            color: theme.palette.error.main,
        },
        orange: {
            color: theme.palette.warning.main,
        },
        green: {
            color: theme.palette.success.main,
        },
        uppercase: {
            textTransform: 'uppercase',
            fontSize: '0.625rem',
            letterSpacing: '1px',
        },
        uppercaseSpacing: {
            textTransform: 'uppercase',
            fontSize: '0.75rem',
            letterSpacing: '1.5px',
        },
        content: {
            fontWeight: 600,
            lineHeight: 'normal',
        },
        contentInfo: {
            fontSize: '1.5rem',
            fontWeight: 800,
            lineHeight: 'normal',
            '@media (max-width: 320px)': {
                fontSize: '1.25rem',
            },
        },
        subContent: {
            fontSize: '0.75rem',
            fontWeight: 600,
            lineHeight: 'normal',
        },
        sentence: {
            fontWeight: 600,
            lineHeight: 1.25,
        },
        sentenceSmall: {
            fontSize: '0.75rem',
            lineHeight: '1.25'
        },
        info: {
            fontSize: '0.875rem',
        },
        link: {
            fontSize: '0.875rem',
            cursor: 'pointer',
        },
        tooltip: {
            fontSize: '0.75rem',
            letterSpacing: '1px',
            fontWeight: 600,
            fontFamily: 'Muli !important',
            [theme.breakpoints.up('md')]: {
                fontSize: '1rem',
            },
        },

        // Number Style
        number: {
            fontFamily: 'Montserrat !important',
        },
        list: {
            fontSize: '0.875rem',
            fontWeight: 500,
            lineHeight: 'normal',
        },
        formHeader: {
            fontSize: '2.25rem',
            fontWeight: 200,
        },
        amount: {
            fontSize: '1.5rem',
            fontWeight: 200,
        },
        smallAmount: {
            fontSize: '12px',
            fontWeight: 200,
        },
        default: {},

        // Money Style
        money: {
            display: 'flex',
            justifyContent: 'flex-end',
            alignItems: 'baseline',
        },

        // Header Style
        header: {
            fontFamily: 'Montserrat !important',
        },
        headerDefault: {
            fontSize: '1.25rem',
            fontWeight: 800,
            letterSpacing: '1.3px',
            textTransform: 'uppercase',
            '@media (max-width: 320px)': {
                fontSize: '1.125rem',
            },
        },
        headerSmall: {
            fontSize: '0.75rem',
            fontWeight: 500,
            letterSpacing: '1.6px',
            textTransform: 'uppercase',
            lineHeight: '1rem',
        },
        subHeader: {
            fontWeight: 600,
        },
        subTitle: {
            letterSpacing: '1px',
        },
        empty: {},
    }),
);

export interface WgTypographyProps extends TypographyProps {
    text?:
        | 'content'
        | 'contentInfo'
        | 'subContent'
        | 'sentence'
        | 'sentenceSmall'
        | 'link'
        | 'tooltip'
        | 'info';
    header?:
        | 'subHeader'
        | 'headerDefault'
        | 'headerSmall'
        | 'subTitle'
        | 'uppercase'
        | 'uppercaseSpacing';
    number?: 'default' | 'list' | 'amount' | 'smallAmount' | 'formHeader';
    colored?: boolean | 'rp' | 'error';
    gutterTop?: string;
    money?: boolean | 'decimalsLowercase';
    percent?: boolean;
    fontSize?: string;
    fitContent?: boolean;
    withSign?: boolean;
    suffix?: string;
    component?: 'div' | 'span' | 'p';
}

function WgTypography({
    children,
    text,
    header,
    number,
    colored,
    gutterTop,
    money,
    percent,
    fontSize,
    fitContent,
    withSign,
    suffix,
    component,
    className,
    ...other
}: WgTypographyProps) {
    const classes = useStyles();
    const color = calculateColor();
    children = calculateChildrenWithSuffix();
    return (
        <Typography
            component={component || 'p'}
            style={{
                marginTop: gutterTop,
                fontSize: `${fontSize || ''}`,
            }}
            className={clsx(
                {
                    [`${classes[text ?? 'empty']}`]: text,
                    [`${classes.header} ${classes[header ?? 'empty']}`]: header,
                    [`${classes.number} ${classes[number ?? 'empty']}`]: number,
                    [classes[color ?? 'empty']]: colored,
                    [classes.fitContent]: fitContent,
                },
                className ?? '',
            )}
            {...other}
        >
            {children}
        </Typography>
    );

    function calculateColor() {
        if (colored && typeof children === 'number') {
            return children < 0 ? 'red' : 'green';
        }
        if (typeof children === 'string') {
            if (typeof colored === 'boolean') {
                return 'green';
            }
            return colored === 'rp' ? 'orange' : 'red';
        }
        return undefined;
    }

    function calculateChildrenWithSuffix() {
        if (typeof children === 'number') {
            const sign = children < 0 ? '-' : '+';
            if (withSign) {
                children = `${sign} ${children}`;
            }
            if (suffix) {
                children = `${children} ${suffix}`;
            }
        }
        return children;
    }
}

export default React.memo(WgTypography);
