import React, {useCallback, useMemo, useState} from 'react';
import {createStyles, Drawer, makeStyles} from '@material-ui/core';
import {getCookieConsent, setCookie} from './Cookie';
import CookieBanner from './CookieBanner';
import {TextType} from "../types/Types";

const useStylesBackdrop = makeStyles(() =>
    createStyles({
        root: {
            display: 'none',
        },
    }),
);
const useStylesDrawer = makeStyles((theme) =>
    createStyles({
        root: {
            height: 0,
        },
        paper: {
            marginLeft: 'auto',
            [theme.breakpoints.up('sm')]: {
                width: '500px',
                marginRight: '16px',
                marginBottom: '16px',
                borderRadius: '4px',
            },
        },
    }),
);

type InitTrackingType = () => void;

const initCookieDrawer = (initTracking: InitTrackingType) => () => {
    const consent = getCookieConsent();
    if (
        consent.marketing ||
        consent.necessary ||
        consent.preferences ||
        consent.statistics
    ) {
        setCookie('CookieConsent', JSON.stringify(consent), 30);
        initTracking();
        return false;
    }
    return true;
}

interface ICookieDrawer {
    initTracking: InitTrackingType;
    text: TextType;
    arrow: string;
}

function CookieDrawer({initTracking, text, arrow}: ICookieDrawer): JSX.Element {
    const classesBackdrop = useStylesBackdrop();
    const classesDrawer = useStylesDrawer();
    const [isOpen, setIsOpen] = useState(initCookieDrawer(initTracking));
    const backdropProps = useMemo(() => ({classes: classesBackdrop}), [
        classesBackdrop,
    ]);
    const onClose = useCallback(() => {
        setIsOpen(false);
        initTracking();
    }, [initTracking]);

    if (isOpen) {
        return (
            <Drawer
                open={isOpen}
                classes={classesDrawer}
                BackdropProps={backdropProps}
                anchor="bottom"
            >
                <CookieBanner onClose={onClose} text={text} arrow={arrow}/>
            </Drawer>
        );
    }
    return <></>;
}

export default React.memo(CookieDrawer);
