export const createGlobalTheme = (palette: Palette, shadows: Shadows) =>
    responsiveFontSizes(
        createMuiTheme({
            palette: palette,
            shadows: shadows,
            typography: {
                fontFamily: 'Muli',
            },
            overrides: {
                MuiFormControl: {
                    root: {
                        width: '100%',
                        fontFamily: 'Muli',
                    },
                },

                MuiAppBar: {
                    colorDefault: {
                        backgroundColor: palette.highlight.main,
                    },
                },
                MuiSelect: {
                    filled: {
                        '&:focus': {
                            backgroundColor: 'transparent',
                        },
                    },
                },
                MuiFilledInput: {
                    root: {
                        fontFamily: 'Muli',
                        borderRadius: 0,
                        backgroundColor: 'transparent',
                        transition: 'none',
                        '&:hover': {
                            backgroundColor: 'transparent !important',
                        },
                        '&$focused': {
                            backgroundColor: 'transparent !important',
                        },
                        '&$disabled': {
                            backgroundColor: 'transparent !important',
                        },
                    },
                    input: {
                        padding: '27px 0 10px',
                        fontFamily: 'Muli',
                        '&$disabled': {
                            cursor: 'default',
                        },
                        '&:-webkit-autofill': {
                            '-webkit-box-shadow':
                                '0 0 0 30px white inset !important',
                            boxShadow: '0 0 0 30px white inset !important',
                        },
                    },
                    multiline: {
                        padding: '27px 0 10px',
                    },
                    underline: {
                        '&:before': {
                            borderBottom: 'none',
                            borderBottomColor: palette.primary.main,
                            transition: 'none',
                        },
                        '&:after': {
                            borderBottom: 'none',
                        },
                        '&:hover:not($disabled):not($focused):not($error):before': {
                            borderBottom: 'none !important',
                        },
                        '&:hover:not($disabled):before': {
                            // borderBottom: `2px solid ${palette.primary.main} !important`
                            borderBottom: 'none !important',
                        },
                        '&$disabled:before': {
                            // borderBottom: `2px solid ${palette.primary.main} !important`
                            borderBottom: 'none !important',
                        },
                    },
                    adornedEnd: {
                        paddingRight: 0,
                    },
                },
                MuiPaper: {
                    elevation2: {
                        boxShadow:
                            '0 0 10px 0 rgba(0, 0, 0, 0.05), 0 1px 3px 0 rgba(0, 0, 0, 0.08)',
                    },
                    rounded: {
                        borderRadius: '5px',
                    },
                },
                MuiInput: {
                    root: {
                        fontFamily: 'Muli',
                        borderRadius: 0,
                        margin: '0 !important',
                    },
                    input: {
                        padding: '27px 0 10px',
                        fontFamily: 'Muli',
                    },
                    multiline: {
                        padding: '27px 0 10px',
                    },
                    underline: {
                        '&:before': {
                            borderBottom: 'none',
                            borderBottomColor: palette.primary.main,
                            transition:
                                'border-bottom-color 2000ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
                        },
                        '&:after': {
                            borderBottom: 'none',
                        },
                        '&:hover:not($disabled):not($focused):not($error):before': {
                            borderBottom: 'none !important',
                        },
                        '&:hover:not($disabled):before': {
                            borderBottom: 'none !important',
                        },
                        '&$disabled:before': {
                            borderBottomStyle: 'none',
                        },
                    },
                },
                MuiInputLabel: {
                    root: {
                        fontFamily: 'Muli',
                    },
                    shrink: {
                        transform:
                            'translate(0px, 10px) scale(0.75) !important',
                        whiteSpace: 'nowrap',
                        textOverflow: 'ellipsis',
                    },
                    filled: {
                        transform: 'translate(0px, 20px) scale(1)',
                    },
                },
                MuiFormHelperText: {
                    contained: {
                        marginLeft: 0,
                        marginRight: 0,
                    },
                    root: {
                        color: palette.error.main,
                        marginTop: '-12px',
                    },
                },
                MuiList: {
                    padding: {
                        paddingTop: '0',
                        paddingBottom: '0',
                    },
                },

                MuiListItem: {
                    root: {
                        '&$disabled': {
                            pointerEvents: 'all',
                        },
                    },
                },
                MuiListItemIcon: {
                    root: {
                        minWidth: '48px',
                    },
                },
                MuiListItemText: {
                    root: {
                        marginBottom: '6px',
                    },
                    primary: {
                        font: 'Muli',
                        fontSize: '1rem',
                        lineHeight: 'normal',
                    },
                },
                MuiFormControlLabel: {
                    root: {
                        marginLeft: '0px',
                        marginRight: '0px',
                    },
                    label: {
                        fontFamily: 'Muli !important',
                        fontSize: '16px !important',
                        lineHeight: 'normal !important',
                        color: palette.text.primary,
                    },
                },

                MuiButton: {
                    root: {
                        borderRadius: '25px',
                        textTransform: 'none',
                        height: '50px',
                        fontFamily: 'Muli',
                        fontWeight: 600,
                        fontSize: '16px',
                        maxWidth: '420px',
                        boxShadow: 'none !important',
                    },
                    containedPrimary: {
                        color: palette.common.white,
                        backgroundColor: palette.secondary.main,
                        '&:hover, &:focus': {
                            backgroundColor: palette.action.hover,
                        },
                        '&$disabled': {
                            color: palette.action.disabled,
                            backgroundColor: palette.action.disabledBackground,
                        },
                    },
                    containedSecondary: {
                        color: palette.secondary.main,
                        backgroundColor: palette.common.white,
                        '&:hover, &:focus': {
                            backgroundColor: palette.action.hover,
                        },
                        '&$disabled': {
                            color: palette.action.disabledBackground,
                        },
                    },
                    contained: {
                        backgroundColor: palette.text.hint,
                        color: palette.text.secondary,
                        '&:hover, &:focus': {
                            backgroundColor: palette.primary.main,
                            color: palette.common.white,
                        },
                        '&$disabled': {
                            color: palette.action.disabled,
                            backgroundColor: palette.action.disabledBackground,
                        },
                    },
                    outlinedPrimary: {
                        color: palette.common.white,
                        borderColor: palette.common.white,
                        borderWidth: '2px',
                        '&:hover, &:focus': {
                            backgroundColor: palette.primary.main,
                        },
                        '&$disabled': {
                            color: palette.action.disabled,
                            borderColor: palette.action.disabledBackground,
                        },
                    },
                    outlinedSecondary: {
                        color: palette.secondary.main,
                        borderColor: palette.secondary.main,
                        borderWidth: '2px',
                        '&:hover, &:focus': {
                            backgroundColor: palette.primary.main,
                            color: palette.common.white,
                        },
                        '&$disabled': {
                            color: palette.action.disabledBackground,
                            borderColor: palette.action.disabledBackground,
                        },
                    },
                },

                MuiMobileStepper: {
                    root: {
                        background: palette.background.default,
                    },
                    dot: {
                        border: 'solid 2px',
                        borderColor: palette.gray.light,
                        backgroundColor: palette.common.white,
                        width: '9px',
                        height: '9px',
                    },
                    dotActive: {
                        borderColor: palette.nav.active,
                        backgroundColor: palette.common.white,
                    },
                },

                /*Button or Listitem, etc: Click animation color*/
                MuiTouchRipple: {
                    ripple: {
                        color: palette.gray.normal,
                    },
                },

                MuiExpansionPanel: {
                    root: {
                        boxShadow: 'none',

                        '&$expanded': {
                            margin: 'auto',
                        },
                        '&:before': {
                            backgroundColor: 'transparent',
                        },
                    },
                },

                MuiExpansionPanelSummary: {
                    root: {
                        padding: '0',
                    },
                    content: {
                        '&$expanded': {
                            margin: '12px 0 !important',
                        },
                        width: 'calc(100% - 16px)',
                    },
                    expandIcon: {
                        marginRight: 0,
                    },
                },

                MuiExpansionPanelDetails: {
                    root: {
                        padding: '8px 18px 0 18px',
                        width: '100%',
                        flexDirection: 'column',
                    },
                },

                MuiContainer: {
                    root: {
                        '@media (max-width: 959px)': {
                            padding: '0 16px',
                        },
                        '@media (min-width: 960px)': {
                            padding: 0,
                        },
                    },
                },

                MuiIconButton: {
                    root: {
                        color: palette.text.primary,
                    },
                },

                MuiChip: {
                    root: {
                        fontFamily: 'Muli',
                    },
                    icon: {
                        marginTop: '2px',
                    },
                    sizeSmall: {
                        fontSize: '0.75rem',
                        height: '20px',
                    },
                },

                MuiDivider: {
                    root: {
                        backgroundColor: palette.text.secondary,
                    },
                    light: {
                        backgroundColor: `${palette.text.secondary}30`,
                    },
                    middle: {
                        marginLeft: '1.25rem',
                        marginRight: '1.25rem',
                    },
                },

                MuiSnackbar: {
                    anchorOriginBottomCenter: {
                        bottom: '40px',
                    },
                },

                MuiPopover: {
                    paper: {
                        minWidth: 'fit-content',
                    },
                },

                MuiTooltip: {
                    tooltipPlacementTop: {
                        margin: '0 0 8px 0',
                        '@media (min-width: 600px)': {
                            margin: '0 0 8px 0',
                        },
                    },
                },
            },
        }),
    );

export const lightTheme = createGlobalTheme(lightThemePalette, shadows);
// noinspection JSUnusedGlobalSymbols
export const darkTheme = createGlobalTheme(darkThemePalette, shadows);
export const psdTheme = createGlobalTheme(psdThemePalette, shadows);
