export function setCookie(
    cookieName: string,
    cookieValue: string,
    expiresInDays = 30,
) {
    const d = new Date();
    d.setTime(d.getTime() + expiresInDays * 24 * 60 * 60 * 1000);
    const expires = `expires=${d.toUTCString()}`;
    document.cookie = `${cookieName}=${cookieValue};${expires};path=/;SameSite=Lax`;
}
export function getAllCookies() {
    return document.cookie.split(';').map((cookie) => cookie.trim());
}
export function getCookie(cookieName: string) {
    const name = `${cookieName}=`;
    const cookies = getAllCookies();
    for (let i = 0; i < cookies.length; i++) {
        let cookie = cookies[i];
        while (cookie.charAt(0) === ' ') {
            cookie = cookie.substring(1);
        }
        if (cookie.indexOf(name) === 0) {
            return cookie.substring(name.length, cookie.length);
        }
    }
    return undefined;
}
export interface ICookieConsent {
    necessary?: boolean;
    preferences?: boolean;
    statistics?: boolean;
    marketing?: boolean;
}

export function getCookieConsent(): ICookieConsent {
    try {
        const cookieConsent = JSON.parse(getCookie('CookieConsent') ?? '{}') as ICookieConsent;
        if (typeof cookieConsent === "object") {
            return cookieConsent;
        }
    } catch (e) {
        console.error(e);
    }
    return {};
}
