import {
    Checkbox,
    createStyles,
    FormControlLabel,
    makeStyles,
} from '@material-ui/core';
import clsx from 'clsx';
import React from 'react';
import {ReactSVG} from 'react-svg';
import WgTypography from './WgTypography';

const useStyles = makeStyles((theme) =>
    createStyles({
        unchecked: {
            color: '#bfbfbf',//theme.palette.gray.bright,
        },
        checkBox: {
            padding: 0,
        },
        disabled: {
            opacity: '70%',
        },
    }),
);

export type CookieChoiceType =
    | 'necessary'
    | 'preferences'
    | 'statistics'
    | 'marketing';

export interface ICookieBannerFormControlLabelProps {
    name: CookieChoiceType;
    checked: boolean;
    disabled: boolean;
    title: string;
    text: string;
}

function CookieBannerFormControlLabel({
    name,
    checked,
    disabled,
    title,
    text,
}: ICookieBannerFormControlLabelProps) {
    const classes = useStyles();

    return (
        <FormControlLabel
            checked={checked}
            control={
                <Checkbox
                    className={classes.checkBox}
                    icon={
                        <ReactSVG
                            className={classes.unchecked}
                            src="/img/checkbox-unchecked.svg"
                        />
                    }
                    checkedIcon={
                        <ReactSVG
                            className={clsx({
                                [classes.disabled]: disabled,
                            })}
                            src="/img/checkbox-checked.svg"
                        />
                    }
                    name={name}
                    disabled={disabled}
                />
            }
            label={
                <>
                    <WgTypography
                        gutterTop="12px"
                        color="textPrimary"
                        text="content"
                    >
                        {title}
                    </WgTypography>
                    <WgTypography
                        gutterBottom
                        color="textPrimary"
                        text="sentenceSmall"
                        className={clsx({
                            [classes.disabled]: disabled,
                        })}
                    >
                        {text}
                    </WgTypography>
                </>
            }
        />
    );
}

export default React.memo(CookieBannerFormControlLabel);
